// -------------------------------------------
// UNIVERSAL SLUG CONTROLLER COMPONENT
// app/components/SlugManager.js
// -------------------------------------------

"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import clsx from "clsx";

/**
 * PROPS:
 * base   → "/projects", "/crm", "/tasks"
 * slug   → "AAS-IT-MOB-001"
 * routes → [
 *   { label: "Overview", path: "overview" },
 *   { label: "Tasks", path: "tasks" },
 *   { label: "Issues", path: "issues" }
 * ]
 *
 * GENERATES CLEAN URLS:
 * /projects/AAS-IT-MOB-001/overview
 * /projects/AAS-IT-MOB-001/tasks
 * /projects/AAS-IT-MOB-001/issues
 */

export default function SlugManager({ base, slug, routes = [], children }) {
  const pathname = usePathname();

  return (
    <div className="w-full">

      {/* Navigation Tabs */}
      <nav className="flex gap-4 border-b border-gray-200 mb-4">
        {routes.map((r) => {
          const url = `${base}/${slug}/${r.path}`;
          const isActive = pathname.startsWith(url);

          return (
            <Link
              key={r.path}
              href={url}
              className={clsx(
                "px-4 py-2 text-sm font-medium border-b-2 transition-all",
                isActive
                  ? "border-blue-600 text-blue-600"
                  : "border-transparent text-gray-500 hover:text-gray-800"
              )}
            >
              {r.label}
            </Link>
          );
        })}
      </nav>

      {/* Auto renders sub pages */}
      <div className="mt-4">
        {children}
      </div>

    </div>
  );
}
